<template>
  <div>
    <h1>奖品管理</h1>
    <ul>
      <li v-for="prize in prizes" :key="prize.id">
        {{ prize.name }} - 概率: {{ prize.probability }} - 库存: {{ prize.quantity }}
        <button @click="updatePrize(prize.id)">更新</button>
      </li>
    </ul>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      prizes: [],
    };
  },
  methods: {
    async fetchPrizes() {
      const response = await axios.get("/api/activity-prizes/1"); // 示例活动 ID
      this.prizes = response.data;
    },
    updatePrize(prizeId) {
      alert(`更新奖品信息: ${prizeId}`);
    },
  },
  mounted() {
    this.fetchPrizes();
  },
};
</script>