<template>
  <div>
    <h1>缓存管理</h1>
    <button @click="cachePrizes">缓存奖品信息</button>
    <button @click="clearCache">清除缓存</button>
    <ul>
      <li v-for="prize in cachedPrizes" :key="prize.id">
        {{ prize.name }} - 概率: {{ prize.probability }} - 库存: {{ prize.quantity }}
      </li>
    </ul>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      cachedPrizes: [],
    };
  },
  methods: {
    async cachePrizes() {
      await axios.post("/api/activity-prizes/redis/1/cache"); // 示例活动 ID
      alert("奖品信息已缓存");
    },
    async fetchCachedPrizes() {
      const response = await axios.get("/api/activity-prizes/redis/1/cache"); // 示例活动 ID
      this.cachedPrizes = response.data;
    },
    async clearCache() {
      await axios.delete("/api/activity-prizes/redis/1/cache"); // 示例活动 ID
      alert("缓存已清除");
      this.cachedPrizes = [];
    },
  },
  mounted() {
    this.fetchCachedPrizes();
  },
};
</script>